import React, { useState, useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  CardMedia,
  FormControlLabel,
  RadioGroup,
  Radio,
  Checkbox,
  Typography
} from '@material-ui/core';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import wait from 'src/utils/wait';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { useHistory } from 'react-router-dom';
import { getVideoQuestion,setShowVideo } from '../../../api/livestreams';
import Chat from '../Chat';
import { getThreads } from 'src/slices/chat';
import { useDispatch } from 'src/store';
import { set } from 'numeral';

const useStyles = makeStyles(() => ({ root: {} }));

const LivestreamViewPage = ({ className, video, ...rest }) => {


  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getThreads());
  }, [dispatch]);
  var title;
  if (video) {
    title = video.title;
  }
  var url;
  if (video) {
    url = video.url;
  }
  const history = useHistory();

  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  let [questions, setQuestions] = useState([]);
  let [show, setShow] = useState('');
  const getLive = useCallback(async () => {
    try {
      const questions = await getVideoQuestion(video.id);
      console.log(questions);
      if (isMountedRef.current) {
        setQuestions(questions);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getLive();
  }, [getLive]);
  const doClick = (e, id) => {
    let v='';
    if (id !== show) {
      v=id
    }
    setShow(v);
    setShowVideo(video.id,v)
  };
  return (
    <Formik
      initialValues={{
        id: video.id || '',
        title: title || '',
        url: url || '',
        question: '',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        title: Yup.string().max(255)
      })}
      onSubmit={async (
        values,
        { resetForm, setErrors, setStatus, setSubmitting }
      ) => {
        try {
          console.log(values);
          // NOTE: Make API request
          await wait(500);
          history.push('/teacher/management/livestream');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        setFieldValue,
        isSubmitting,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              <input type="hidden" name="id" required value={values.id} />
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.title && errors.title)}
                    fullWidth
                    helperText={touched.title && errors.title}
                    label="Tiêu đề"
                    name="title"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.title}
                    variant="outlined"
                  />
                </Grid>

                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.content_type && errors.content_type)}
                    fullWidth
                    helperText={touched.content_type && errors.content_type}
                    label="Loại Content"
                    name="content_type"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    disabled
                    value="livestream"
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <Typography gutterBottom variant="h6" color="textPrimary">
                    Question
                  </Typography>
                  <Typography
                    gutterBottom
                    variant="body2"
                    color="textSecondary"
                  >
                    Chọn câu hỏi để hiển thị trên video
                  </Typography>

                  <List className={classes.questions}>
                  <Grid container spacing={3}>
                {questions.map((quest) => {
                  const labelId = `checkbox-list-tag-${quest.id}`;

                  return (
                    <Grid item md={3} xs={12} key={quest.id}>
                    <ListItem key={quest.id} role={undefined} dense button  onClick={e => {
                      doClick(e, quest.id);
                    }}>
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          checked={quest.id == show}
                          tabIndex={-1}
                          disableRipple
                          inputProps={{ 'aria-labelledby': labelId }}
                        />
                      </ListItemIcon>
                      <ListItemText id={quest.id} primary={`${quest.title}`} />
                    </ListItem>
                    </Grid>
                  );
                })}
              </Grid>
                  </List>
                </Grid>
                <Grid item md={6} xs={12}>
                  <CardMedia
                    component="iframe"
                    height="360"
                    image={values.url + '?autoplay=1'}
                    title="Live from space album cover"
                    allow="autoPlay"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <Chat/>
                </Grid>
                
              </Grid>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

LivestreamViewPage.propTypes = {
  className: PropTypes.string,
  video: PropTypes.object.isRequired
};

export default LivestreamViewPage;
