import React, {
  useState,
  useCallback,
  useEffect
} from 'react';
import PropTypes from 'prop-types';
import { Box, Container, makeStyles } from '@material-ui/core';
import axios from 'src/utils/axios';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import LivestreamViewPage from './LivestreamViewPage';
import Header from './Header';
import { getVideoApi } from '../../../api/livestreams';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const LivestreamView = (props) => {
  const { videoId } = props.match.params;
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [video, setVideo] = useState(null);

  const getVideo = useCallback(async () => {
    try {
      let video = await getVideoApi(videoId);

      if (isMountedRef.current) {
        setVideo(video);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getVideo();}, [getVideo]);

  if (!video) {
    return null;
  }

  return (
    <Page className={classes.root} title="Content Edit">
      <Container maxWidth={false}>
        <Header/>
      </Container>
      <Box mt={3}>
        <Container maxWidth="lg">
          <LivestreamViewPage video={video}/>
        </Container>
      </Box>
    </Page>
  );
};
export default LivestreamView;
